html, body, h1, h2, h3, h4, h5, h6, p, a, form, select, option, label {
  font-family: 'Open Sans', sans-serif; }

h1 {
  font-size: 48px;
  font-size: 3rem;
  font-weight: 600; }
  @media only screen and (max-width: 767px) {
    h1 {
      font-size: 38px;
      font-size: 2.375rem; } }

h2 {
  font-size: 28px;
  font-size: 1.75rem;
  font-weight: 800;
  color: #067FBC; }
  @media only screen and (max-width: 767px) {
    h2 {
      font-size: 28px;
      font-size: 1.75rem; } }

h3 {
  font-size: 20px;
  font-size: 1.25rem;
  font-weight: 800; }
  @media only screen and (max-width: 767px) {
    h3 {
      font-size: 20px;
      font-size: 1.25rem; } }

h4 {
  font-size: 16px;
  font-size: 1rem;
  color: #818992; }
  @media only screen and (max-width: 767px) {
    h4 {
      font-size: 16px;
      font-size: 1rem; } }

h5 {
  font-size: 14px;
  font-size: 0.875rem;
  font-weight: 800;
  text-transform: uppercase; }
  @media only screen and (max-width: 767px) {
    h5 {
      font-size: 14px;
      font-size: 0.875rem; } }

h6 {
  font-size: 12px;
  font-size: 0.75rem;
  font-weight: 800; }
  @media only screen and (max-width: 767px) {
    h6 {
      font-size: 12px;
      font-size: 0.75rem; } }

p {
  color: #2A2F32; }

p.lead {
  color: #818992;
  font-size: 20px;
  font-size: 1.25rem;
  line-height: 1.25;
  margin: 1em 0; }

p.secondary-lead {
  color: #2A2F32;
  font-size: 20px;
  font-size: 1.25rem;
  line-height: 1.25;
  margin: 1em 0; }

a {
  color: #067FBC; }

a:hover,
a:focus {
  text-decoration: none; }

a.link {
  text-transform: uppercase;
  font-weight: 600;
  color: #205984;
  cursor: pointer; }
  a.link .fa {
    color: #F58220; }
  a.link:hover, a.link:focus {
    color: #067FBC; }

/* ------------------- PlaceHolder Text ------------------ */
.form-control::-webkit-input-placeholder {
  color: #2A2F32; }

.form-control:-moz-placeholder {
  /* Firefox 18- */
  color: #2A2F32; }

.form-control::-moz-placeholder {
  /* Firefox 19+ */
  color: #2A2F32; }

.form-control:-ms-input-placeholder {
  color: #2A2F32; }

.has-warning .form-control::-webkit-input-placeholder,
.has-error .form-control::-webkit-input-placeholder {
  color: #C82828; }

.has-warning .form-control:-moz-placeholder,
.has-error .form-control:-moz-placeholder {
  color: #C82828; }

.has-warning .form-control::-moz-placeholder,
.has-error .form-control::-moz-placeholder {
  color: #C82828; }

.has-warning .form-control:-ms-input-placeholder,
.has-error .form-control:-ms-input-placeholder {
  color: #C82828; }

.form-control[disabled]::-webkit-input-placeholder,
.form-control[readonly]::-webkit-input-placeholder,
fieldset[disabled] .form-control::-webkit-input-placeholder {
  color: #818992; }

.form-control[disabled]:-moz-placeholder,
.form-control[readonly]:-moz-placeholder,
fieldset[disabled] .form-control:-moz-placeholder {
  color: #818992; }

.form-control[disabled]::-moz-placeholder,
.form-control[readonly]::-moz-placeholder,
fieldset[disabled] .form-control::-moz-placeholder {
  color: #818992; }

.form-control[disabled]:-ms-input-placeholder,
.form-control[readonly]:-ms-input-placeholder,
fieldset[disabled] .form-control:-ms-input-placeholder {
  color: #818992; }

/* -------------- Outlined Button -------------------- */
.btn-default {
  border-color: #F58220;
  color: #2A2F32;
  font-size: 14px;
  font-size: 0.875rem; }

.btn-default:hover,
.btn-default:focus,
.btn-default:active {
  color: #F9F9F9;
  background-color: #F58220;
  border-color: #F58220; }

/* ---------------- Solid Button ---------------------- */
.btn-solid, .btn-dark {
  background-color: #F58220;
  border-color: #F58220;
  color: #F9F9F9;
  font-size: 14px;
  font-size: 0.875rem; }

.btn-solid:hover, .btn-dark:hover,
.btn-solid:focus,
.btn-dark:focus,
.btn-solid:active,
.btn-dark:active {
  background-color: #d8690a;
  color: #F9F9F9; }

/* ---------------- Dark Button --------------------------- */
.btn-dark:hover,
.btn-dark:focus,
.btn-dark:active {
  background-color: transparent; }

.input-group-btn .btn {
  border-width: 1px;
  background-color: #F58220;
  color: #F9F9F9; }

.input-group-btn .btn:hover {
  background-color: #d8690a; }

.sr-only {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  height: 1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

h1 {
  color: red; }

figure {
  display: block;
  max-width: 400px;
  float: right; }

figcaption {
  font-style: italic;
  text-align: center; }

img {
  max-width: 100%; }
